.flexbox-column {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.flexbox-search {
  display: flex;
  align-items: center;
  justify-content: center;
}

.myTitle {
  padding-top: 25px;
}

.nes-input {
  margin-right: 10px;
  width: auto;
}

.form-box {
  padding: 1rem 5rem;
}

.create-link button {
  margin-top: 10px !important;
}

.input {
  margin: 1rem 0;
}

.form-header {
  text-align: center;
  font-size: 1.2rem;
  padding: 0.2rem;
}

.form-container {
  margin: 0px auto;
}

.home-content h1 {
  font-size: 15px;
  margin: 25px 0;
  padding-top: 22px;
}

.link-listings {
  display: flex;
  flex-direction: column;
  text-align: center;
  align-items: center;
  width: 30%;
}

.ui.card,
.ui.cards > .card {
  box-shadow: none !important;
  width: 100%;
}

.home-top {
  text-align: center;
  padding: 25px 0;
}

.text-top {
  text-align: center;
}

.nes-btn {
  font-size: 25px;
}

.link-card {
  width: 100%;
}

.ui.card > .content {
  padding: 0 !important;
}

.ui.card {
  padding: 1.5rem 0 !important;
}

.flexbox-space {
  width: 60%;
  display: flex;
  justify-content: space-around;
}
